export default {
	data() {
		return {
			user:{
				Username:'',
				Password:'',
				email:''
			},
			register0:false
		};
	},
	methods:{
		login(){
			if(!this.register0){
			console.log(this.user)
			uni.showToast({
				title: '登陆成功',
				icon: 'none',
			});
			setTimeout(()=>{
				uni.switchTab({
					url:'../2/2'
				})
			},1500)}else{
				this.register0=false
			}
		},
		register(){
			if(!this.register0){
				this.register0=true
			}else{
				uni.showToast({
					title: '注册成功',
					icon: 'none',
				});
				this.register0=false
			}
		}
	}
}
